

.container{
    width: 50%;
    height: 40vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
  
}

.button__wrapper{
    width: 200px;
    height: 70px;
    margin: 0 5px;
    
}
.button{
    min-width: 100%;
    min-height: 100%;
    
    border: none;
    background-color: #282c34;
    color: white;
    border-radius: 16px;
    cursor: pointer;
}