.item {
    /* background-color: white; */
    margin-left: 5px;
    min-width: 90%;
    height: 60px;
    border-radius: 10px;
    display: flex;
    flex-direction: row;
    cursor: pointer;
    margin-bottom: 2px;
    text-decoration: none;


}

.avatar__wrapper {
    min-width: 34px;
    min-height: 34px;
    width: 34px;
    height: 34px;
    position: relative;
}

.text__wrapper {
    margin: 0 0 0 12px;
    height: 100%;
    width: 70%;
    /* background-color: rgba(0, 0, 255, 0.329); */
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.name,
.message {
    margin: 0;
    /* display: block; */
    text-align: start;
    color: rgb(88, 155, 249);
}

.name {
    font-size: small;
}