.container{
    min-width: 100%;
    min-height: 100%;
    /* margin: 25px; */
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(212, 115, 220);
    background: linear-gradient(45deg, rgba(138,90,162,1) 0%, rgb(155, 64, 200) 50%, rgb(225, 0, 255) 100%,  rgb(255, 34, 196) 100%);
    font-size: large;
    animation: gradient 7s ease infinite;
    background-size: 5000% 5000%;
    color: rgb(255, 255, 255);
    /* transition-property: width height;
    transition-duration: 10s; */
}
.title{
    margin: 0 !important;
}
@keyframes gradient{
 
    0%{
        background-position: 0 50%;
    }
    50%{
        background-position: 100% 50%;
    }
    100%{
        background-position: 0 50%;
    }
}