.frame {
    height: 88vh;
    max-height: calc(100vh - 75px);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background: rgb(53, 50, 61);
    background: linear-gradient(45deg, rgb(65, 65, 80) 0%, rgb(36, 39, 53) 50%, rgb(135, 83, 134) 100%, rgb(235, 93, 192) 100%);
    font-size: large;
    animation: gradient 60s ease infinite;
    background-size: 15000% 15000%;
    
}

.title {
    
    margin-top: 40px;
    text-align: center;
    display: block;
}

@keyframes gradient {

    0% {
        background-position: 0 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0 50%;
    }
}

.items {
    min-height: 32vh;
    padding: 2px;
    min-width: 92%;
    border-radius: 15px;
    background-color: rgb(255, 255, 255);
    display: flex;
    flex-direction: row;
    justify-content: center;
    justify-content: flex-start;

}

.value {
   
    margin-left: 30px;
    display: inline-block;
    text-align: center;
}

.item {
    padding: 0 7px;
    margin: 7px 4px;
    height: 50px;
    width: 98%;
    border-radius: 12px;
    border: 0.5px solid grey;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.status {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.action__wrapper {
    margin-right: 10px;


}

.radio__wrapper {
    width: 200px;
    display: flex;
    flex-direction: row;
}

@media screen and (max-width: 927px) {
    .status {
        min-width: 87%;
    }
}

@media screen and (max-width: 550px) {

    .status {
        min-width: 84%;
        width: 84%;
    }

}

@media screen and (max-width: 470px) {

    .status {
        min-width: 77%;
        width: 77%;
    }

    .value {
        margin-left: 50px;

    }
}

@media screen and (max-width: 400px) {

    .status {
        min-width: 73%;
        width: 73%;
    }

    .value {
        margin-left: 50px;
    }
}


.action {
    cursor: pointer;
}